import { useEffect, useState } from 'react';

export default function Test() {
  const [count, setCount] = useState(0);
  const [age, setAge] = useState(20);

  useEffect(() => {
    if (count !== 0 && count < 3) {
      setAge(age + 1);
    }
  }, [count]);
  return (
    <div>
      <h1></h1>
      <div>
          {age} / {count}
      </div>
      <button onClick={() => setCount(count + 1)}></button>
    </div>
  );
}
