import { useContext, useEffect, useState } from 'react';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import styled from 'styled-components';
import { Context1 } from '../App';
import { addItem } from '../store';
import { useDispatch } from 'react-redux';
import useLike from '../hooks/like';
import useUser from '../hooks/user';
/**
 * styled-components 
 * 1. styled.
 * 2. styled.`css `
 *   
 *   js   
 */
const YellowBtn = styled.button`
  background: yellow;
  color: black;
  padding: 10px;
`;
const CustomBtn = styled.button`
  background: ${(props) => props.bg || 'yellow'};
  color: ${(props) => props.color || 'black'};
  padding: 10px;
`;
const Box = styled.div`
  background: gray;
  padding: 20px;
`;
export default function Detail(props) {
  const { id } = useParams();
  const shoes = props.shoes.find((item) => item.id === Number(id));
  const [count, setCount] = useState(0);
  const [alert, setAlert] = useState(true);
  const [input, setInput] = useState('');
  const [error, setError] = useState(false);

  const [tab, setTab] = useState(0);

  const dispatch = useDispatch();

  //     
  useEffect(() => {});
  // mount 1    
  useEffect(() => {
    const watched = localStorage.getItem('watched');
    if (watched) {
      const watchedList = JSON.parse(watched);
      watchedList.push(shoes.id);
      const set = new Set(watchedList);
      localStorage.setItem('watched', JSON.stringify(Array.from(set)));
    } else {
      localStorage.setItem('watched', JSON.stringify([shoes.id]));
    }
  }, []);
  //  state    
  useEffect(() => {}, [count]);
  // unmount  1   
  useEffect(() => {
    return () => {};
  }, []);

  useEffect(() => {
    const timer = setTimeout(() => {
      setAlert(false);
    }, 2000);
    // console.log('count', count);
    // clean up function
    return () => {
      // useEffect   
      clearTimeout(timer);
      // unmount  
    };
    // count   , []   mount  
  }, [count]);

  useEffect(() => {
    const reg = /^[0-9]*$/g;
    const result = reg.test(input);
    if (!result) {
      setError(true);
    } else {
      setError(false);
    }
  }, [input]);

  const [fade, setFade] = useState('');
  useEffect(() => {
    setFade('end');
    return () => {
      setFade('');
    };
  }, []);

  const addCart = (item) => {
    dispatch(
      addItem({
        id: item.id,
        name: item.title,
        count: 1,
      })
    );
  };
  const [like, addLike] = useLike();
  const [name] = useUser();

  return !shoes ? (
    <div> .</div>
  ) : (
    <div className={`start ${fade}`}>
      <Container>
        <Box>
          {/* <CustomBtn
            bg="blue"
            color="white"
            onClick={() => setCount(count + 1)}
          >
            
          </CustomBtn> */}
          <YellowBtn></YellowBtn>
          <input type="text" onChange={(e) => setInput(e.target.value)} />
        </Box>
        {alert ? (
          <div className="alert alert-warning">2   </div>
        ) : null}
        {error ? (
          <div className="alert alert-danger"> .</div>
        ) : null}
        {name}
        <Row>
          <Col md={6}>
            <img src={shoes.img} width="100%" alt="" />
          </Col>
          <Col md={6}>
            <h4 className="pt-5">{shoes.title}</h4>
            <p>{shoes.content}</p>
            <p>{shoes.price}</p>

            <button className="btn btn-danger" onClick={() => addCart(shoes)}>
              
            </button>
            <button onClick={addLike}>{like}</button>
          </Col>
        </Row>

        <Nav variant="tabs" defaultActiveKey="link0">
          <Nav.Item>
            <Nav.Link eventKey="link0" onClick={() => setTab(0)}>
              0
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link1" onClick={() => setTab(1)}>
              1
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link2" onClick={() => setTab(2)}>
              2
            </Nav.Link>
          </Nav.Item>
        </Nav>
        <TabContent tab={tab} />
        {/* <TabContent tab={tab} shoes={props.shoes} /> */}
      </Container>
    </div>
  );
}

function TabContent({ tab }) {
  // const test = useContext(Context1);
  // console.log(test);
  const { shoes } = useContext(Context1);
  const [fade, setFade] = useState('');
  useEffect(() => {
    const setFadeTimeout = setTimeout(() => setFade('end'), 100);
    return () => {
      clearTimeout(setFadeTimeout);
      setFade('');
    };
  }, [tab]);
  return (
    <div className={`start ${fade}`}>
      {
        [<div>{shoes[0].title}</div>, <div>content1</div>, <div>content2</div>][
          tab
        ]
      }
    </div>
  );
}
